export function exportHead() {
  const head = [];
  head.push("Code");
  head.push("Name");
  head.push("sTT");
  head.push("Major opportunities chance");
  head.push("Major name");
  head.push("Available status");
  return head;
}

export function exportRow(data) {
  const row = [];
  row.push(data.maKeHoach);
  row.push(data.tenHocKy);
  row.push(data.sTT);
  row.push(data.nganhDaoTao.coHoiNgheNghiep);
  row.push(data.nganhDaoTao.nganhDaoTao.ten);
  row.push(data.isDeleted ? "Deleted" : "Available");
  return row;
}
